import { createRouter, createWebHashHistory } from "vue-router";

const routes = [
  {
    path: "/",
    name: "Home",
    component: () => import("@/components/view/HomeView.vue"),
  },
  {
    path: "/Intro",
    name: "intro",
    component: () => import("@/components/view/IntroView.vue"),
  },

  {
    path: "/service",
    name: "service",
    component: () => import("@/components/view/ServiceView.vue"),
  },
  {
    path: "/news",
    name: "news",
    component: () => import("@/components/view/NewsView.vue"),
  },
  {
    path: "/news/11",
    name: "newsdetail11",
    component: () => import("@/components/view/detail_news/news11.vue"),
  },
  {
    path: "/news/10",
    name: "newsdetail10",
    component: () => import("@/components/view/detail_news/news10.vue"),
  },
  {
    path: "/news/9",
    name: "newsdetail9",
    component: () => import("@/components/view/detail_news/news9.vue"),
  },
  {
    path: "/news/8",
    name: "newsdetail8",
    component: () => import("@/components/view/detail_news/news8.vue"),
  },
  {
    path: "/news/7",
    name: "newsdetail7",
    component: () => import("@/components/view/detail_news/news7.vue"),
  },
  {
    path: "/news/6",
    name: "newsdetail6",
    component: () => import("@/components/view/detail_news/news6.vue"),
  },
  {
    path: "/news/5",
    name: "newsdetail5",
    component: () => import("@/components/view/detail_news/news5.vue"),
  },
  {
    path: "/news/4",
    name: "newsdetail4",
    component: () => import("@/components/view/detail_news/news4.vue"),
  },
  {
    path: "/news/3",
    name: "newsdetail3",
    component: () => import("@/components/view/detail_news/news3.vue"),
  },
  {
    path: "/news/2",
    name: "newsdetail2",
    component: () => import("@/components/view/detail_news/news2.vue"),
  },
  {
    path: "/news/1",
    name: "newsdetail1",
    component: () => import("@/components/view/detail_news/news1.vue"),
  },
  {
    path: "/contact",
    name: "contact",
    component: () => import("@/components/view/ContactView.vue"),
  },
  {
    path: "/employ",
    name: "employ",
    component: () => import("@/components/view/EmployView.vue"),
  },
  {
    path: "/header",
    name: "Header",
    component: () => import("@/components/header/HeaderNav.vue"),
  },
  {
    path: "/menu",
    name: "menu",
    component: () => import("@/components/header/Menu.vue"),
  },
  {
    path: "/test",
    name: "test",
    component: () => import("@/components/view/test.vue"),
  },

  {
    path: "/footer",
    name: "Footer",
    component: () => import("@/components/footer/Footer.vue"),
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
  scrollBehavior() {
    return { top: 0 };
  },
});

export default router;
